<template>
  <div>
    <v-card width="50%" class="mx-auto mt-5">
      <v-data-table
        :headers="headers"
        :items="configuracoes"
        :hide-default-footer="true"
        disable-pagination
        disable-filtering
        disable-sort
        :loading="loading"
        loading-text="Carregando"
        no-data-text="Nenhuma configuração encontrada"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Configurações de horário</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  Nova configuração
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-form @submit.prevent ref="form">
                      <v-row>
                        <v-col cols="6" sm="6">
                          <v-text-field
                            v-model="editedItem.nome_configuracao"
                            label="Nome da configuração"
                            :rules="[rules.required]"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6">
                          <v-text-field
                            v-model="editedItem.numero_dias"
                            label="Número de dias"
                            type="number"
                            :disabled="editedItem.id_configuracao != null"
                            :rules="[
                              rules.required,
                              rules.greaterThanZero(true),
                              rules.lessThanOrEqual(7, true),
                            ]"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-form>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancelar
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save"> OK </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <!-- eslint-disable-next-line -->
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="open(item)"> mdi-eye </v-icon>
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="confirmDelete(item)">
            mdi-delete
          </v-icon>
          <v-icon small class="mr-2" @click="cloneConfiguration(item)">
            mdi-content-copy
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
    <v-dialog v-model="deleteDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="text-h5">
          Deseja mesmo excluir esta configuração de horário?
        </v-card-title>
        <v-card-text>Todas informações associadas serão perdidas</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="deleteItem(selectedDeleteItem)"
            :loading="loading"
          >
            Excluir
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="deleteDialog = false"
            :disabled="loading"
          >
            Cancelar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="cloneDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="text-h5"> Clonar configuração? </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="cloneConfigurationConfirm(selectedCloneItem)"
          >
            Clonar
          </v-btn>
          <v-btn text color="primary" @click="cloneDialog = false">
            Cancelar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import validationMixin from "@/mixins/validation";

export default {
  name: "ListaConfiguracoes",
  mixins: [validationMixin],

  data() {
    return {
      configuracoes: [],
      dialog: false,
      deleteDialog: false,
      cloneDialog: false,
      selectedDeleteItem: null,
      selectedCloneItem: null,
      editedIndex: -1,
      editedItem: {
        nome_configuracao: null,
        numero_dias: null,
        id_configuracao: null,
      },
      loading: false,
      headers: [
        { text: "Nome", value: "nome_configuracao" },
        { text: "Grades salvas", value: "count" },
        { text: "Ações", value: "actions", sortable: false, width: "150px" },
      ],
    };
  },

  created() {
    this.load();
  },

  watch: {
    dialog(newVal) {
      if (newVal === true) {
        this.resetValidation();
      }
    },
  },

  methods: {
    async load() {
      try {
        this.loading = true;
        this.$store.dispatch("setSelectedSchedule", null);
        this.$store.dispatch("setSelectedPeriod", null);
        const resp = await this.$axios.get("/configuracao");
        if (resp.status === 200) {
          this.configuracoes = resp.data.configuracoes;
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.loading = false;
      }
    },

    editItem(item) {
      this.editedIndex = this.configuracoes.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      if (!this.validate()) {
        return;
      }

      const res = await this.$axios.post("/configuracao", {
        id_configuracao: this.editedItem.id_configuracao
          ? this.editedItem.id_configuracao
          : null,
        nome_configuracao: this.editedItem.nome_configuracao,
        numero_dias: this.editedItem.numero_dias,
      });
      if (res.status === 200) {
        if (this.editedIndex > -1) {
          Object.assign(this.configuracoes[this.editedIndex], this.editedItem);
        } else {
          this.editedItem.id_configuracao = res.data.id_configuracao;
          this.configuracoes.push(this.editedItem);
        }
        this.close();
        if (res.data.id_configuracao != null) {
          this.open({ id_configuracao: res.data.id_configuracao });
        } else {
          this.load();
        }
      }
    },

    open(item) {
      this.$router.push({
        name: "Passos",
        params: { idConfiguracao: item.id_configuracao },
      });
    },

    confirmDelete(item) {
      this.deleteDialog = true;
      this.selectedDeleteItem = item;
    },

    cloneConfiguration(item) {
      this.selectedCloneItem = item;
      this.cloneDialog = true;
    },

    async cloneConfigurationConfirm(item) {
      const resp = await this.$axios.post("/configuracao/clone", {
        id_configuracao: item.id_configuracao,
      });
      if (resp.status === 200) {
        this.load();
        this.cloneDialog = false;
      }
    },

    async deleteItem(item) {
      this.loading = true;
      try {
        await this.$axios.delete("/configuracao", {
          data: { id_configuracao: item.id_configuracao },
        });
        this.deleteDialog = false;
        this.load();
      } catch (e) {
        console.error(e);
      } finally {
        this.loading = false;
      }
    },
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nova Configuração"
        : "Editar Configuração";
    },
  },
};
</script>
